const yearInput = document.querySelector("#yearInput");
const checkButton = document.querySelector("#checkButton");
const resultDisplay = document.querySelector("#result");

function isLeapYear(year) {
    return (year % 4 === 0 && year % 100 !== 0) || (year % 400 === 0);
}

checkButton.addEventListener("click", (event) => {
    event.preventDefault()
    const year = parseInt(yearInput.value, 10);

if (Number(year) && year > 0) {
    const isLeap = isLeapYear(year);
    resultDisplay.textContent = isLeap ? "–í–∏ –Ω–∞—Ä–æ–¥–∏–ª–∏—Å—è —É –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫!" : "–í–∏ –Ω–∞—Ä–æ–¥–∏–ª–∏—Å—è –Ω–µ —É –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫!";
    resultDisplay.style.color = isLeap ? "green" : "red";
} else {
    resultDisplay.textContent = "–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å –∫–æ—Ä–µ–∫—Ç–Ω–∏–π —Ä—ñ–∫.";
    resultDisplay.style.color = "darkred";
}
    yearInput.value = "";
});

yearInput.addEventListener("keydown", (event) => {
  if (event.key === "Enter") {
    // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞, —á–∏ –Ω–∞—Ç–∏—Å–Ω—É—Ç–∞ –∫–ª–∞–≤—ñ—à–∞ Enter
    event.preventDefault(); // –ó–∞–ø–æ–±—ñ–≥–∞—î –≤–≤–µ–¥–µ–Ω–Ω—é —Å–∏–º–≤–æ–ª–∞ —É –ø–æ–ª–µ

    const year = parseInt(yearInput.value, 10);

    if (!isNaN(year) && year > 0) {
      // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –Ω–∞ –∫–æ—Ä–µ–∫—Ç–Ω–µ —á–∏—Å–ª–æ
      const isLeap = isLeapYear(year);
      resultDisplay.textContent = isLeap
        ? "–í–∏ –Ω–∞—Ä–æ–¥–∏–ª–∏—Å—è —É –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫!"
        : "–í–∏ –Ω–∞—Ä–æ–¥–∏–ª–∏—Å—è –Ω–µ —É –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫!";
      resultDisplay.style.color = isLeap ? "green" : "red";
    } else {
      resultDisplay.textContent = "–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å –∫–æ—Ä–µ–∫—Ç–Ω–∏–π —Ä—ñ–∫.";
      resultDisplay.style.color = "darkred";
    }
  }
});
