let count = 0; 
let timeLeft = 110; 
let gameActive = false; 
let timerInterval;
let preparationTime = 10; 
let startTime;
const clickLimit = 10000; 
let bonusGiven = false;
let triplePointsActive = false; 
let doublePointsActive = false; 
let lastClickTime = 0; 
let comboClicks = 0; 
let freezeTimes = [50, 25]; 
let freezeActive = false; 
const startButton = document.getElementById("start-button");
const timerDisplay = document.getElementById("timer-clicker");
const clickArea = document.getElementById("click-area");
const clickCount = document.getElementById("click-count");
const clicksText = document.getElementById("clicks-text");
const resultDisplay = document.getElementById("result");
const statusText = document.getElementById("status-text");
const phaseDescription = document.getElementById("phase-description");

setTimeout(() => { startButton.removeAttribute("disabled"); }, 3000);

function startGame() {
    count = 0;
    timeLeft = 110;
    gameActive = false;
    bonusGiven = false;
    triplePointsActive = false;
    doublePointsActive = false;
    lastClickTime = 0;
    comboClicks = 0;
    clickCount.textContent = count;
    resultDisplay.textContent = "";
    statusText.textContent = "–ü—ñ–¥–≥–æ—Ç–æ–≤—á–∞ —Ñ–∞–∑–∞";
    clickArea.classList.remove("clicker__click-area--inactive", "clicker__click-area--post-war");
    clickArea.classList.add("clicker__click-area--preparation");
    clicksText.classList.add("clicker__clicks-text--hidden");
    phaseDescription.textContent = "–ì–æ—Ç—É–π—Ç–µ—Å—è! –ì—Ä–∞ –ø–æ—á–Ω–µ—Ç—å—Å—è —á–µ—Ä–µ–∑ 10 —Å–µ–∫—É–Ω–¥.";
    timerDisplay.style.color = "rgb(0, 191, 255)"; 
    let prepTimer = preparationTime;
    timerDisplay.textContent = `00:${prepTimer}`;
    startButton.classList.add("clicker__start-button--hidden");
    let prepInterval = setInterval(() => {
        prepTimer--;
        console.log(timerDisplay)
        timerDisplay.innerHTML = `00:${prepTimer < 10 ? '0' + prepTimer : prepTimer}`;
        timerDisplay.style.color = `rgb(0, ${128 + (prepTimer / 10) * 127}, 0)`; 
        if (prepTimer <= 0) {
            clearInterval(prepInterval);
            beginClickPhase();
        }
        
    }, 1000);
}
function beginClickPhase() {
    gameActive = true;
    startTime = new Date();
    statusText.textContent = "–ê–∫—Ç–∏–≤–Ω–∞ —Ñ–∞–∑–∞";
    clickArea.classList.remove("clicker__click-area--preparation");
    clickArea.classList.add("clicker__click-area--active");
    clicksText.classList.remove("clicker__clicks-text--hidden");
    phaseDescription.textContent = "–ö–ª—ñ–∫–∞–π—Ç–µ –ø–æ –ø–æ–ª—é, —â–æ–± –Ω–∞–±—Ä–∞—Ç–∏ 10,000 –æ—á–æ–∫ —ñ –∑–Ω–∏—â–∏—Ç–∏ –∑–ª–æ!–ß–∞—Å –æ–±–º–µ–∂–µ–Ω–∏–π ‚Äì 1 —Ö–≤ 50 —Å–µ–∫,  –Ø–∫—â–æ –∑–∞ 55 —Å–µ–∫ —É –≤–∞—Å –º–µ–Ω—à–µ 5000 –æ—á–æ–∫ ‚Äì –æ—Ç—Ä–∏–º–∞—î—Ç–µ +375 –æ—á–æ–∫ —ñ x3 –º–Ω–æ–∂–Ω–∏–∫ –Ω–∞ 10 —Å–µ–∫. –ù–∞ 50—Å —Ç–∞ 25—Å –≥—Ä–∞ –∑–∞–º–æ—Ä–æ–∂—É—î—Ç—å—Å—è –Ω–∞ 5 —Å–µ–∫ (–∫–ª—ñ–∫–∏ –Ω–µ —Ä–∞—Ö—É—é—Ç—å—Å—è), –ø—ñ—Å–ª—è —á–æ–≥–æ –≤–∏ –æ—Ç—Ä–∏–º—É—î—Ç–µ +500 –æ—á–æ–∫. –£ –æ—Å—Ç–∞–Ω–Ω—ñ 5 —Å–µ–∫ –º–Ω–æ–∂–Ω–∏–∫ –∑—Ä–æ—Å—Ç–∞—î –≤ 4 —Ä–∞–∑–∏. –®—Ç—Ä–∞—Ñ: —è–∫—â–æ –≤–∏ –Ω–µ –∫–ª—ñ–∫–∞—î—Ç–µ 9 —Å–µ–∫ (–ø—ñ—Å–ª—è 6,000 –æ—á–æ–∫), –Ω–∞—Å—Ç—É–ø–Ω–∏–π –∫–ª—ñ–∫ -3,000 –æ—á–æ–∫. –ö–æ–∂–Ω—ñ 1,000 –æ—á–æ–∫ –¥–∞—é—Ç—å +100 –æ—á–æ–∫ —ñ +3 —Å–µ–∫.üî• –ì–æ—Ç–æ–≤—ñ –¥–æ –±–æ—é? ";
    updateTimer();
}
function updateTimer() {
    timerInterval = setInterval(() => {
        if (freezeActive) return; 
        timeLeft--;
        timerDisplay.textContent = `00:${timeLeft < 10 ? '0' + timeLeft : timeLeft}`;
        if (timeLeft > 55) {
            let progress = (110 - timeLeft) / 55;
            timerDisplay.style.color = `rgb(0, ${255 - progress * 127}, 0)`;
        } else if (timeLeft > 25) {
            let progress = (55 - timeLeft) / 30;
            timerDisplay.style.color = `rgb(${progress * 255}, ${255 - progress * 127}, 0)`; 
        } else {
            timerDisplay.style.color = "red";
        }
        if (freezeTimes.includes(timeLeft)) {
            activateFreeze();
        }
        if (timeLeft <= 5) {
            activateCrazyMode();
        }
        if (timeLeft <= 55 && count < 5000 && !bonusGiven) {
            giveBonus();
        }
        if (timeLeft <= 0) {
            endGame();
        }
    }, 1000);
}
function activateFreeze() {
    freezeActive = true;
    clickArea.style.backgroundColor = "gray";
    timerDisplay.style.color = "gray";
    count += 500; 
    clickCount.textContent = `${count}`; 
    setTimeout(() => {
        freezeActive = false;
        clickArea.style.backgroundColor = "yellow";
        timerDisplay.style.color = "red";
    }, 5000); 
}

function activateCrazyMode() {
    triplePointsActive = true;
}

function giveBonus() {
    bonusGiven = true;
    count += 375;
    triplePointsActive = true;
    setTimeout(() => {
        triplePointsActive = false;
    }, 10000); 
    timeLeft += 10; 
    clickCount.textContent = `${count}`;
}
function handleClick() {
    if (gameActive && !freezeActive) {
        let currentTime = new Date().getTime();
        let elapsedTime = (currentTime - lastClickTime) / 1000;
        lastClickTime = currentTime;
        if (elapsedTime < 1) {
            comboClicks++;
            if (comboClicks >= 3) {
                count += 10; 
                comboClicks = 0;
            }
        } else {
            comboClicks = 1;
        }
        let points = triplePointsActive ? 80 : doublePointsActive ? 40 : (timeLeft <= 10 ? 40 : 20); 
        count += points;
         if (count >= 6000 && elapsedTime > 9) { 
            count -= 3000; 
        }
        clickCount.textContent = `${count}`;
        if (count >= clickLimit) {
            endGame();
        }
    }
}
function handleKeyPress(event) {
    if (event.key === "u" || event.key === "U") {
        if (gameActive && !freezeActive) {
            count -= 1;
            clickCount.textContent = `${count}`;
        }
    }
}
function endGame() {
    clearInterval(timerInterval);
    gameActive = false;
    clickArea.classList.remove("clicker__click-area--active");
    clickArea.classList.add("clicker__click-area--post-war");
    if (count < clickLimit) {
        statusText.textContent = "–ü—ñ—Å–ª—è –≤–æ—î–Ω–Ω–∞ —Ñ–∞–∑–∞";
        phaseDescription.textContent = "–ó–ª–æ –ø–µ—Ä–µ–º–æ–≥–ª–æ, —Ç–∞ –Ω–µ–∑–¥–∞–≤–∞–π—Å—è —ñ –¥–æ–±—Ä–æ –π–æ–≥–æ –ø–µ—Ä–µ–º–æ–∂–µ.";
    } else {
        statusText.textContent = "–ü—ñ—Å–ª—è –≤–æ—î–Ω–Ω–∞ —Ñ–∞–∑–∞";
        phaseDescription.textContent = "–í–∏ –ø–µ—Ä–µ–º–æ–≥–ª–∏! –ó–ª–æ –≤—ñ–¥—Å—Ç—É–ø–∏–ª–æ, –∞–ª–µ –Ω–µ–Ω–∞–¥–æ–≤–≥–æ.";
    }
    clicksText.classList.add("clicker__clicks-text--hidden");
    startButton.classList.remove("clicker__start-button--hidden");
}
startButton.addEventListener("click", startGame);
clickArea.addEventListener("click", handleClick);
document.addEventListener("keydown", handleKeyPress);