document.addEventListener("DOMContentLoaded", function () {
    const fortunes = [
        "–°—å–æ–≥–æ–¥–Ω—ñ —Ç–≤—ñ–π –¥–µ–Ω—å!",
        "–ó–≤—ñ—Å–Ω–æ –¥–µ–Ω—å –º–æ–∂–µ –±—É—Ç–∏ –ø–æ–≥–∞–Ω–∏–º! –ê–ª–µ —Ç–æ—á–Ω–æ –∑–∞–≤—Ç—Ä–∞ –ø–æ—â–∞—Å—Ç–∏—Ç—å",
        "–ì–æ—Ç—É–π—Å—å –¥–æ –∑–º—ñ–Ω!",
        "–¢—Ä–µ–±–∞ –∑—É—Å–∏–ª–ª—è –¥–ª—è —É—Å–ø—ñ—Ö—É!",
        "–°—å–æ–≥–æ–¥–Ω—ñ –ë—ñ–±–ª—ñ—è, –∑–∞–≤—Ç—Ä–∞ –ö–æ–±–∑–∞—Ä!",
        "–ô–¥–∏ –≤–ø–µ—Ä–µ–¥, –∞–ª–µ –ø–µ—Ä—ñ–æ–¥–∏—á–Ω–æ –∑—É–ø–∏–Ω—è–π—Å—è!",
        "–ß–∞—Å –≤–∏–∫–æ–Ω–∞—Ç–∏ –Ω–µ–º–æ–∂–ª–∏–≤—É –º—Ä—ñ—é!",
        "–ù–µ –≥—Ä—ñ—à–∏! –ù–µ —Å–º—ñ—Ç–∏! –ù–µ –∑–∞–∑–¥—Ä–∏!",
        "–ù—ñ—á–æ–≥–æ –≥–∞—Ä–Ω–æ–≥–æ, –Ω—ñ—á–æ–≥–æ –ø–æ–≥–∞–Ω–æ–≥–æ –Ω–µ –±—É–¥–µ –±–µ–∑ —Ä–∏–∑–∏–∫—É",
        "–£ —á–æ–º—É—Å—å –ø–æ—Ç—É–∂–Ω–æ–º—É —î –ê—Ö—ñ–ª–ª–µ—Å–æ–≤–∞ –ø‚Äô—è—Ç–∞.",
        "-.. / .-.- / -.- / ..-- / --.. / .- / --. / .-. / ..- / -.-.--",
        "–°–µ—Ä–¥–µ—á–∫–æ + –£–∫—Ä–∞—ó–Ω–∞ + –°—Ç—Ä—ñ–ª–∫–∞ + –°–æ–Ω—Ü–µ + –°—Ç—Ä—ñ–ª–∫–∞ + –í—ñ—Ç–µ—Ä + –¢—Ä–∞–≤–∞ + –í–æ–¥–∞"
    ];

    document.getElementById("cookieBtn").addEventListener("click", function () {
        let selectedFortunes = [];
        let usedIndexes = new Set();

        while (selectedFortunes.length < 3) {
            let randomIndex = Math.floor(Math.random() * fortunes.length);
            if (!usedIndexes.has(randomIndex)) {
                usedIndexes.add(randomIndex);
                selectedFortunes.push(fortunes[randomIndex]);
            }
        }

        let fortunesDiv = document.getElementById("fortunes");
        fortunesDiv.innerHTML = ""; 

        selectedFortunes.forEach((fortune) => {
            let div = document.createElement("div");
            div.textContent = fortune;
            div.style.padding = "8px";
            div.style.margin = "10px 0"; 
            div.style.border = "1px solid #d4b600";
            div.style.borderRadius = "5px";
            div.style.textAlign = "center";
            div.style.fontSize = "14px"; 
            div.style.fontWeight = "bold";

            fortunesDiv.appendChild(div);
        });
    });
});
